html{font-family:"微軟正黑體",sans-serif;font-weight:normal;font-size:1rem;line-height:1.5;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}@media only screen and (min-width: 0){html{font-size:.9375rem}}@media only screen and (min-width: 786px){html{font-size:1rem}}@media only screen and (min-width: 1800px){html{font-size:1.1rem}}fieldset,p,table,pre,hr{margin-bottom:1rem;margin-top:0}.giga,.mega,h1,.h1,h2,.h2,h3,.h3{font-weight:700;line-height:1.2}.giga small,.giga .small,.mega small,.mega .small,h1 small,h1 .small,.h1 small,.h1 .small,h2 small,h2 .small,.h2 small,.h2 .small,h3 small,h3 .small,.h3 small,.h3 .small{font-size:65%;color:color("black", "secondary")}.giga+hr,.mega+hr,h1+hr,.h1+hr,h2+hr,.h2+hr,h3+hr,.h3+hr{margin-top:-0.25rem}h4,.h4,h5,.h5,h6,.h6{line-height:1.2}h4 small,h4 .small,.h4 small,.h4 .small,h5 small,h5 .small,.h5 small,.h5 .small,h6 small,h6 .small,.h6 small,.h6 .small{font-size:75%;color:color("black", "secondary")}h4+hr,.h4+hr,h5+hr,.h5+hr,h6+hr,.h6+hr{margin-top:-0.25rem}.giga{font-size:4rem}.mega{font-size:3rem}h1,.h1{font-size:2.5rem;margin:0 0 1rem 0}h2,.h2{font-size:2rem;margin:0 0 .8rem 0}h3,.h3{font-size:1.5rem;margin:0 0 .6rem 0}h4,.h4{font-size:1.25rem;margin:0 0 .5rem 0;font-weight:600}h5,.h5{font-size:1rem;margin:0 0 .4rem 0;font-weight:600}h6,.h6{font-size:.875rem;margin:0 0 .35rem 0;font-weight:600}.sideNote{font-size:.875rem}em,.em{font-style:italic}strong,.strong{font-weight:600}small,.small{font-size:50%}.light{font-weight:300}.thin{font-weight:200}div{box-sizing:border-box}a{color:#ff4500;text-decoration:none;outline:none;border:none}a:active,a:hover,a:focus{outline:none;border:none}hr{border-bottom:1px solid color("black", "disabled");border-left:0;border-right:0;border-top:0;margin:1rem 0}blockquote{border-left:4px solid color("primary", "base");color:color("black", "secondary");font-style:italic;margin:1.5rem 0 1.5rem 0;padding-left:1rem}